nav {
    z-index: 100;
}

.navbar__logo {
    width: 10%;
    padding: 0px 30px 0px 0px;
}

.navbar__brand {
    width: 70%;
}

.navbar-toggler {
    outline: none !important;
    border: none !important;
}

@media only screen and (max-width: 600px) { 
    .navbar__logo {
        width: 20%;
        padding: 0px 10px 0px 0px;
    }
    nav {
        display: flex;

    }
}